<template>
  <div class='log-wrap'>
    <div class='g-form'>
      <div class='g-form-line'>
        <span class="g-form-label">用&nbsp&nbsp户&nbsp&nbsp名:</span>
        <div class='g-form-input'>
          <input v-model='usernameModel' type='text' placeholder='请输入用户名'/>
        </div>
        <span class="g-form-error">{{userNameErrors.errorText}}</span>
      </div>
      <div class='g-form-line'>
        <span class="g-form-label">密&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp 码:</span>
        <div class='g-form-input'>
          <input v-model='userpasswordModel' type='password' placeholder='请输入密码'/>
        </div>
        <span class="g-form-error">{{userPasswordErrors.errorText}}</span>
      </div>
      <div class='g-form-line'>
        <span class="g-form-label">确认密码&nbsp:</span>
        <div class='g-form-input'>
          <input type='password' v-model='userpasswordRepeatModel' placeholder='请重新输入密码'/>
        </div>
        <span class="g-form-error">{{userPasswordRepeatErrors.errorText}}</span>
      </div>
      <div class='g-form-line'>
        <div class='g-form-btn'>
          <a class='button' @click='onRegin'>注册</a>
        </div>
      </div>
      <p>{{errorText}}</p>
    </div>
        <div class='pic-log'>
        <img src='../assets/pic/reg.jpg'>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      usernameModel:'',
      userpasswordModel:'',
      userpasswordRepeatModel:'',
      errorText:''
    }
  },
  computed:{
    userNameErrors(){
      let errorText,status
      if(!/@/g.test(this.usernameModel)){
        errorText='不包含@'
        status=false
      }else{
         errorText=''
         status=true
      }
      if(!this.userNameFlag){
        errorText=''
        this.userNameFlag=true
      }
      return {
        errorText,
        status
      }
    },
    userPasswordErrors(){
      let errorText,status
      if(!/^\w{1,6}$/g.test(this.userpasswordModel)){
        errorText='请设置1~6位的密码'
        status=false
      }else{
         errorText=''
         status=true
      }
      if(!this.userPasswordFlag){
        errorText=''
        this.userPasswordFlag=true
      }
      return {
        errorText,
        status,
      }
    },
    userPasswordRepeatErrors(){
      let errorText,status
      if(this.userpasswordRepeatModel!=this.userpasswordModel&&this.userpasswordRepeatModel!=''){
        errorText='密码不一致'
        status=false
      }else{
         errorText=''
         status=true
      }
      if(!this.userPasswordRepeatFlag){
        errorText=''
        this.userPasswordRepeatFlag=true
      }
      return {
        errorText,
        status
      }
    }
  },
  methods:{
    onRegin(){
      if(!this.userNameErrors.status||!this.userPasswordErrors.status||!this.userPasswordRepeatErrors.status){
        this.errorText = '部分选项未通过!'
      }else{
        this.errorText = '注册成功！'
        this.$http.post('api/login',{"username":this.usernameModel,"password":this.userpasswordModel}).then((res)=>{
          this.$emit('reg-log',res.data)
        },(error)=>{
          console.log(error)
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.log-wrap{
  width:100%;
  font-size:16px;
  clear:both;
  overflow:hidden;
}
.g-form-line{
  width:100%;
  height:30px;
  clear:both;
  overflow:hidden;
  margin:10px;
}
.g-form{
  display:inline-block;
  width:60%;
  float:left;
}
.pic-log{
  display:inline-block;
  width:40%;
  float:left;
  height:150px;
}
.pic-log img{
  height:150px;
}
.g-form-label{
  float:left;
  height:30px;
  line-height:30px;
}
.g-form-input{
  float:left;
  width:60px;
  height:30px;
  margin-left:40px;
}
.g-form-input input{
   padding:5px;
}
.g-form-btn{
  float:left;
  margin-left:142px;
}
.button{
  display:inline-block;
  text-align:center;
  line-height:30px;
  width:60px;
  height:30px;
  background:#4fc08d;
  border:none;
  color:white;
  cursor:pointer;
}
.g-form-error{
  float:left;
  margin-left:150px;
  color:red;
  font-size:12px;
  height:30px;
  line-height:30px;
}
.g-form p{
  color:red;
  font-size:12px;
  margin-left:150px;
}
</style>



