<template>
  <div class="dialog-wrap">
    <div class='dialog-cover' v-if="isShow" @click="closeMyself"></div>
    <transition name='drop'>
      <div class='dialog-content' v-if="isShow">
        <p class='dialog-close' @click="closeMyself">×</p>
        <slot>empty</slot>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  props:{
    isShow:{
      type:Boolean,
      default:true
    }
  },
  methods:{
    closeMyself(){
      this.$emit('on-close')
    }
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.drop-enter-active{
  transition:all 0.5s ease
}
.drop-leave-active{
  transition:all 0.3s ease
}
.drop-enter{
  transform:translateY(-500px);
}
.drop-leave-active{
  transform:translateY(-500px);
}
.dialog-wrap{
  position:fixed;
  width:100%;
  height:100%;
}
.dialog-cover{
  position:fixed;
  background:#000;
  opacity:0.5;
  z-index:5;
  top:0;
  left:0;
  width:100%;
  height:100%;
}
.dialog-content{
  position:fixed;
  width:50%;
  max-height:80%;
  max-width:50%;
  overflow:auto;
  background:#fff;
  top:20%;
  left:50%;
  margin-left:-25%;
  z-index:10;
  border:2px solid #464068;
  padding:2%;
  line-height:1.6;
}
.dialog-close{
  position:absolute;
  top:5px;
  right:5px;
  width:20px;
  height:20px;
  cursor:pointer;
  line-height:20px;
  text-align:center;
}
.dialog-close:hover {
  color: #4fc08d;
}
</style>



