<template>
  <div class='log-wrap'>
    <div class='g-form'>
      <div class='g-form-line'>
        <span class="g-form-label">用户名:</span>
        <div class='g-form-input'>
          <input v-model='usernameModel' type='text' placeholder='请输入用户名'/>
        </div>
        <span class="g-form-error">{{ userErrors.errorText }}</span>
      </div>
      <div class='g-form-line'>
        <span class="g-form-label">密&nbsp&nbsp&nbsp&nbsp码:</span>
        <div class='g-form-input'>
          <input v-model='userpasswordModel' type='password' placeholder='请输入密码'/>
        </div>
        <span class="g-form-error">{{ passwordErrors.errorText }}</span>
      </div>
      <div class='g-form-line'>
        <div class='g-form-btn'>
          <a class='button' @click='onLogin'>登录</a>
        </div>
      </div>
      <p>{{ errorText }}</p>
    </div>
    <div class='pic-log'>
        <img src='../assets/pic/log.jpg'>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      usernameModel:'1',
      userpasswordModel:'',
      errorText:'',
    }
  },
  computed:{
    userErrors(){
      let errorText,status
      if(!/@/g.test(this.usernameModel)){
        errorText='不包含@'
        status=false
      }else{
        errorText=''
        status=true
      }
      if(!this.userFlag){
        errorText=''
        this.userFlag=true
      }
      return {
        errorText,
        status
      }
    },
    passwordErrors(){
      let errorText,status
      if(!/^\w{1,6}$/g.test(this.userpasswordModel)){
        errorText='请设置1~6位的密码'
        status=false
      }else{
        errorText=''
        status=true
      }
      if(!this.passwordFlag){
        errorText=''
        this.passwordFlag=true
      }
      return {
        errorText,
        status
      }
    }
  },
  methods:{
    onLogin(){
      if(!this.userErrors.status||!this.passwordErrors.status){
        this.errorText = '部分选项未通过!'
      }else{
        this.errorText = '登录成功！'
        this.$http.get('api/login').then((res)=>{
          if(this.usernameModel!==res.data.username){
            this.errorText ='无此用户！'
          }else if(this.userpasswordModel!==res.data.password){
            this.errorText ='密码错误！'
          }else{
            this.$emit('has-log',res.data)
          }
        },(error)=>{
          console.log(error)
        })
      }
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.log-wrap{
  width:100%;
  font-size:16px;
  clear:both;
  overflow:hidden;
}
.g-form{
  display:inline-block;
  width:60%;
  float:left;
}
.pic-log{
  display:inline-block;
  width:40%;
  float:left;
  height:150px;
}
.pic-log img{
  height:150px;
}
.g-form-line{
  width:100%;
  height:30px;
  clear:both;
  overflow:hidden;
  margin:10px;
}
.g-form-label{
  float:left;
  height:30px;
  line-height:30px;
}
.g-form-input{
  float:left;
  width:60px;
  height:30px;
  margin-left:70px;
}
.g-form-input input{
   padding:5px;
}
.g-form-btn{
  float:left;
  margin-left:125px;
}
.button{
  display:inline-block;
  text-align:center;
  line-height:30px;
  width:60px;
  height:30px;
  background:#4fc08d;
  border:none;
  color:white;
  cursor:pointer;
}
.g-form-error{
  float:left;
  margin-left:150px;
  color:red;
  font-size:12px;
  height:30px;
  line-height:30px;
}
.g-form p{
  color:red;
  font-size:12px;
  margin-left:130px;
}
</style>


