<template>
  <div class='index-wrap'>
    <div class='index-left'>
      <div class='index-left-block'>
        <h2>全部产品</h2>
        <template v-for='product in productList'>
          <h3>{{product.title}}</h3>
          <ul>
            <li v-for='item in product.list'>
              <a :href='item.url'>{{item.name}}</a>
              <span v-if='item.hot' class='hot-tage'>HOT~</span>
            </li>
          </ul>
          <div v-if='!product.last' class='hr'></div>
        </template>

      </div>
      <div class='index-left-block lastest-news'>
        <h2>最新消息</h2>
        <ul>
          <li v-for='item in newsList' :title='item.title'>
            <a :href='item.url' class='new-item'>{{item.title}}</a>
          </li>
        </ul>
      </div>
    </div>
    <div class='index-right'>
      <slide-show :slides='slidess' @onchange='doSomethingOnSlideChange'></slide-show>
      <div class='index-board-list'>
        <div class='index-board-item' v-for='(item,index) in boardList' :class="['index-board-'+item.id,{'line-last':(index+1)%2===0}]">
          <div class='index-board-item-inner'>
            <h2>{{item.title}}</h2>
            <p>{{item.description}}</p>
            <div class='index-board-button'>
              <router-link class='button' :to="{path:'detail'}">立即购买</router-link>
            </div>
          </div>
