      newsList:[{title:'此项目的绝大部分页面内容是通过在跟目录下创建的data.json文件渲染到页面上的，是模拟从后端请求数据的，开发调试时用’npm run dev’命令，会借助node启动一个本地服务器，可以正常的渲染出相应的数据。而通过打包后的项目时属于静态资源的，点击index.html查看项目，是无法加载mock的数据的，浏览器也会报跨域的错误。若想正常的加载mock数据，最好的方式是讲打包后的资源丢到服务器中。'}],
      productList:{
        pc:{
          title:'PC产品',
          list:[
            {
              name:'数据统计',
              url:'http://starcraft.com'
            },
            {
              name:'数据预测',
              url:'http://warcraft.com'
            },
            {
              name:'流量分析',
              url:'http://overwatch.com',
              hot:true
            },
            {
              name:'广告发布',
              url:'http://hearstone.com'
            }
          ]
        },
        app:{
          title:'手机应用类',
          last:true,
          list:[
            {
              name:'91助手',
              url:'http://xeixin.com'
            },
            {
              name:'产品助手',
              url:'http://twitter.com'
            },
            {
              name:'智能地图',
              url:'http://maps.com',
              hot:true
            },
            {
              name:'团队语音',
              url:'http://phone.com'
            }
          ]
        }
