}
.index-left-block .hr{
  border-bottom:1px solid #95c7e2;
  margin:5px 15px 20px;
}
.index-left-block h2{
  background:#4fc08d;
  color:#fff;
  padding:10px 15px;
  margin-bottom:20px;
}
.index-left-block h3{
  padding:0 15px 5px 15px;
  font-weight:bold;
  color:#222;
}
