<template>
  <div class="counter-component">
    <div class='counter-btn' @click="minus">-</div>
    <div class='counter-show'>
      <input type='text' v-model='number' @keyup="fixNumber"/>
    </div>
    <div class='counter-btn' @click="add">+</div>
  </div>
</template>

<script>
export default {
  props:{
    max:{
      type:Number,
      default:5
    },
    min:{
      type:Number,
      default:1
    }
  },
  data(){
    return {
      number:this.min
    }
  },
  watch:{
    number(){
      this.$emit('on-change',this.number)
    }
  },
  methods:{
    fixNumber(){
      let fix
      if(typeof this.number==='string'){
        fix=Number(this.number.replace(/\D/g,''))
      }else{
        fix=this.number
      }
      if(fix<this.min||fix>this.max){
        fix=this.min
      }
      this.number=fix
    },
    minus(){
      if(this.number<=this.min){
        return
      }
      this.number--
    },
    add(){
      if(this.number>=this.max){
        return
      }
      this.number++
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.counter-component{
  display:inline-block;
  position:relative;
  overflow:hidden;
  vertical-align: middle;
}
.counter-btn{
  display:inline-block;
  border: 1px solid #e3e3e3;
  float:left;
  height:25px;
  line-height:25px;
  width:25px;
  text-align:center;
  cursor:pointer;
}
.counter-btn:hover {
  border-color: #4fc08d;
  background: #4fc08d;
  color: #fff;
}
.counter-show{
  float:left
}
.counter-show input{
  border: none;
  border-top: 1px solid #e3e3e3;
  border-bottom: 1px solid #e3e3e3;
  height:23px;
  line-height:23px;
  outline: none;
  width:30px;
  padding:1px 4px;
  text-align:center;
  outline: none;
}
</style>



