<template>
  <div>
    <this-dialog :is-show="isShowCheckDialog" @on-close="checkStatus">
      <h1>请检查你的支付状态！</h1>
      <div class="button" @click="checkStatus">
        支付成功
      </div>
      <div class="button" @click="checkStatus">
        支付失败
      </div>
    </this-dialog>
    <this-dialog :is-show="isShowSuccessDialog" @on-close="toOrderList">
      购买成功！
    </this-dialog>
    <this-dialog :is-show="isShowFailDialog" @on-close="toOrderList">
      购买失败！
    </this-dialog>
  </div>
</template>

<script>
import Dialog from './dialog'
export default {
  props:{
    isShowCheckDialog:{
      type:Boolean,
      default:false
    },
    orderId:{
      type:[String,Number]
    }
  },
  components:{
    thisDialog:Dialog
  },
  data () {
    return {
      isShowSuccessDialog: false,
      isShowFailDialog: false
    }
  },
  methods: {
    checkStatus () {
      this.$http.post('/api/checkOrder', {
       "orderId": this.orderId
      })
      .then((res) => {
        console.log(res.data)
        this.isShowSuccessDialog = true
        this.$emit('on-close-check-dialog')
      }, (err) => {
        this.isShowFailDialog = true
        this.$emit('on-close-check-dialog')
      })
    },
    toOrderList(){
       this.$router.push({path: '/orderList'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.button{
  width:80px;
  padding:5px 10px;
  background:#4fc08d;
  color:#fff;
  text-align:center;
  cursor:pointer;
  display:inline-block;
  margin-top:10px;
  margin-right:10px;
}
</style>



