<template>
  <div>
    <div class='app-head'>
      <div class='app-head-inner'>
        <router-link :to='{path:"/"}'>
          <img src='../assets/logo.png'>
        </router-link>
        <div class='head-nav'>
          <ul class='nav-list'>
            <li class='active'>{{username}}</li>
            <li v-if="username===''" @click='logClick' class='active'>登录</li>
            <li class='nav-pile'>|</li>
            <li v-if="username===''" @click='regClick' class='active'>注册</li>
            <li v-if="username!==''" class='active' @click='quit'>退出</li>
            <li class='nav-pile'>|</li>
            <li @click='aboutClick' class='active'>关于</li>
          </ul>
        </div>
      </div>
    </div>
    <div class='app-content'>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <div class='app-foot'>
      <p>© 2016 fishenal MIT</p>
    </div>
    <my-dialog :is-show='isShowaboutDialog' @on-close="closeDialog('isShowaboutDialog')">
      <p>本报告在调研数据的基础上，采用定性与定量相结合的方式深入分析了专车市场发展的驱动因素与阻碍因素、专车市场背后的产业格局、专车企业的竞争格局、用户对专车市场的依赖程度、专车对其他交通工具运力的补充效应等，通过这五个章节的研究反映专车市场的发展态势和面临的问题。报告力求客观、深入、准确地反映中国专车市场发展情况，为政府、企事业单位和社会各界提供决策依据。 </p>
    </my-dialog>
    <my-dialog :is-show='isShowlogDialog' @on-close="closeDialog('isShowlogDialog')">
      <log-form @has-log='onSuccessLog' @usermodel='userVal'>rrr</log-form>
    </my-dialog>
    <my-dialog :is-show='isShowregDialog' @on-close="closeDialog('isShowregDialog')">
      <reg-form @reg-log='onSuccessReg'>tgg</reg-form>
    </my-dialog>
  </div>
</template>

<script>
import Dialog from './dialog'
import RegForm from './reg'
import LogForm from './log'
export default {
  components:{
    MyDialog:Dialog,
    RegForm,
    LogForm,

  },
  data () {
    return {
      isShowaboutDialog:false,
      isShowregDialog:false,
      isShowlogDialog:false,
      username:'',
    }
  },
  methods:{
    aboutClick(){
      this.isShowaboutDialog=true
    },
    regClick(){
      this.isShowregDialog=true
    },
    logClick(){
      this.isShowlogDialog=true
    },
    closeDialog(attr){
      this[attr]=false
    },
    userVal(usernameModel){
      this.Val=usernameModel
    },
    onSuccessLog(data){
        this.username=data.username
        this.closeDialog('isShowlogDialog')
    },
    quit(){
      this.username=''
    },
    onSuccessReg(data){
      this.closeDialog('isShowregDialog')
      console.log(data)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hground,meanu,nav,output,ruby,section,summary,time,mark,audio,video{
  margin:0;
  padding:0;
  border:0;
  font-size:100%;
  font:inherit;
  vertical-aligin:baseline
}
article,aside,details,figcaption,figure,footer,header,hground,menu,nav,section{
  displack:block;
}
body{
  line-height:1
}
ol,ul{
  list-style:none
}
blockquote,q{
  quotes:none
}
blockquote:before,blockqoote:after,q:before,q:after{
  content:'';
  content:none
}
body{
  background:#f0f2f5;
  font-family:'Helvetica Neue',Helvetica,Arial,'Hiragino Sans GB','Hiragio Sans GB W3','Microsoft YaHei UI','Microsoft YaHei','WenQuanYi Micro Hei',sans-serif;
  font-size:14px;
  color:#444
}
.app-head{
  background:#363636;
  color:#b2b2b2;
  height:90px;
  line-height:90px;
  width:100%
}
.app-head-inner{
  width:1200px;
  margin:0 auto
}
.head-logo{
  float:left
}
.app-head-inner img{
  width:50px;
  margin-top:20px
}
.head-nav{
  float:right
}
.head-nav ul{
  overflow:hidden;
}
.head-nav li{
  cursor:pointer;
  float:left;
}
.nav-pile{
  padding:0 10px;
}
.app-foot{
  text-align:center;
  height:80px;
  width:100%;
  line-height:80px;
  background:#e3e4e8;
  clear:both;
  margin-top:30px;
}
.container{
  width:1200px;
  margin:0 auto;
}
.active:hover{
  color:gray;
}
</style>



